import axios from "axios";
export default {
  props: ["title"],
  data: () => ({
    snack: false,
    snackColor: "",
    snackText: "",
    valid: false,
    user: {
      email: "",
      password: ""
    },
    passwordRules: [v => !!v || "Password is required", v => v.length >= 8 || "Password is required"],
    emailRules: [v => !!v || "The email is required", v => /.+@.+\..+/.test(v) || "Invalid email"]
  }),
  methods: {
    async userLogin() {
      if (this.$refs.form.validate()) {
        try {
          var res = await axios.post("http://localhost:3000/api/user/login", this.user);
          localStorage.setItem("token", res.data.token);
          this.$store.commit("updateUser", res.data.user);
          this.snack = true;
          this.snackColor = "succes";
          this.snackText = "Succesfully Login";
          this.$router.go("/home");
        } catch (err) {
          console.log(err);
          console.log(err.response);
          this.snack = true;
          this.snackColor = "error";
          this.snackText = "User or password wrong";
        }
      } else {
        this.snack = true;
        this.snackColor = "error";
        this.snackText = "Put the required data";
      }
    }

  }
};